/* ===[ Unstyled ]=== */

.link-unstyled, .link-unstyled:hover, .link-unstyled:focus {
color: $text-color;
text-decoration: none!important;
}
.link-unstyled:focus {
  outline: none;
}
  
table a.link-unstyled:not(.btn), .table a.link-unstyled:not(.btn) {
  color: $text-color;
}

/* ===[ Block ]=== */

.link-block {
  display: block;
  height: 100%;
}


/* ===[ No padding ]=== */

.no-padding {
padding-left: 0 !important;
padding-right: 0 !important;
}

/* ===[ Condensed ]=== */

.form-condensed {
.form-group {
margin-bottom: $table-condensed-cell-padding;
//padding: $table-condensed-cell-padding;
}
}

.list-condensed {
margin-bottom: $table-condensed-cell-padding;
}

@media (max-width: 768px){
.nav-justified-not-stacked > li {
display: table-cell;
width: 1%;
}

.nav-stacked-width-only {
  > li {
    float: none;
    + li {
      margin-top: 2px;
      margin-left: 0; // no need for this gap between nav items
    }
  }
}
}

/* ===[ ol with styled numbers ]=== */

.list-number-surrounded {
counter-reset: section;
list-style-type: none;
}

.list-number-surrounded li:before {
counter-increment: section;
content: counter(section) " ";
margin-right: 1em;
float:left;
vertical-align: -15%;
}


/* ===[ Surrounded icon or number ]=== */

.list-number-surrounded li:before,
.surrounded{
display:inline-block;
text-align: center;
border: solid 2px;
width: 1em;
height: 1em;
line-height: .85em!important;
}

.list-number-surrounded-lg li:before,
.surrounded-lg{
width: (4em / 3);
height: (4em / 3);
line-height: 1.2em!important;
}

.list-number-surrounded-2x li:before,
.surrounded-2x{
width: 2em;
height: 2em;
line-height: 1.8em!important;
}

.list-number-surrounded-3x li:before,
.surrounded-3x{
width: 3em;
height: 3em;
line-height: 2.9em!important;
}

.list-number-surrounded-4x li:before,
.surrounded-4x{
width: 4em;
height: 4em;
line-height: 4em!important;
}

.list-number-surrounded-5x li:before,
.surrounded-5x {
width: 5em;
height: 5em;
line-height: 5em!important;
}
.list-number-surrounded li,
.list-number li {
float: left;
}
