/* ICONS ======================================================================================== */

// Variables
// --------------------------

$icon-font-size-base:   $font-size-base;
$icon-border-color:     darken($body-bg, 15%);
$icon-inverse:          $body-bg;
$icon-li-width:         (30em / 14);

// Base Class Definition
// -------------------------

/*.glyphicon {
  display: inline-block;
  font: normal normal normal $icon-font-size-base/1 FontAwesome; // shortening font declaration
  font-size: inherit; // can't have font-size inherit on line above, so need to override
  text-rendering: auto; // optimizelegibility throws things off #1094
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transform: translate(0, 0); // ensures no half-pixel rendering in firefox
}*/

// Icon Sizes
// -------------------------
.glyphicon-lg {
  font-size: (4em / 3);
  line-height: (3em / 4);
  vertical-align: -15%;
}
.glyphicon-2x { font-size: 2em; }
.glyphicon-3x { font-size: 3em; }
.glyphicon-4x { font-size: 4em; }
.glyphicon-5x { font-size: 5em; }

// List Icons
// -------------------------

.glyphicon-ul {
  padding-left: 0;
  margin-left: $icon-li-width;
  list-style-type: none;
  > li { position: relative; }
}
.glyphicon-li {
  position: absolute;
  left: -$icon-li-width;
  width: $icon-li-width;
  top: (2em / 14);
  text-align: center;
  &.glyphicon-lg {
    left: (-$icon-li-width + (4em / 14));
  }
}
// Bordered & Pulled
// -------------------------

.glyphicon-border {
  padding: .2em .25em .15em;
  border: solid .08em $icon-border-color;
  border-radius: .1em;
}

.glyphicon {
  &.pull-left { margin-right: .3em; }
  &.pull-right { margin-left: .3em; }
}

// Fixed Width Icons
// -------------------------
.glyphicon-fw {
  width: (18em / 14);
  text-align: center;
}

// Stacked Icons
// -------------------------

.glyphicon-stack {
  position: relative;
  display: inline-block;
  width: 2em;
  height: 2em;
  line-height: 2em;
  vertical-align: middle;
}
.glyphicon-stack-1x, .glyphicon-stack-2x {
  position: absolute;
  left: 0;
  width: 100%;
  text-align: center;
}
.glyphicon-stack-1x { line-height: inherit; }
.glyphicon-stack-2x { font-size: 2em; }
.glyphicon-inverse { color: $icon-inverse; }

// Mixins
// --------------------------

@mixin glyphicon-icon() {
  display: inline-block;
  font: normal normal normal $icon-font-size-base/1 FontAwesome; // shortening font declaration
  font-size: inherit; // can't have font-size inherit on line above, so need to override
  text-rendering: auto; // optimizelegibility throws things off #1094
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transform: translate(0, 0); // ensures no half-pixel rendering in firefox
}

@mixin glyphicon-icon-rotate($degrees, $rotation) {
  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=$rotation);
  -webkit-transform: rotate($degrees);
      -ms-transform: rotate($degrees);
          transform: rotate($degrees);
}

@mixin glyphicon-icon-flip($horiz, $vert, $rotation) {
  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=$rotation, mirror=1);
  -webkit-transform: scale($horiz, $vert);
      -ms-transform: scale($horiz, $vert);
          transform: scale($horiz, $vert);
}

// Animated Icons
// --------------------------

.glyphicon-spin {
  -webkit-animation: fa-spin 2s infinite linear;
          animation: fa-spin 2s infinite linear;
}

.glyphicon-pulse {
  -webkit-animation: fa-spin 1s infinite steps(8);
          animation: fa-spin 1s infinite steps(8);
}

@-webkit-keyframes fa-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

@keyframes fa-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

/* ============================================================================================== */

/* columns of same height styles */

.row-full-height {
  height: 100%;
}
.col-full-height {
  height: 100%;
  vertical-align: middle;
}
.row-same-height {
  display: table;
  width: 100%;
  /* fix overflow */
  table-layout: fixed;
}
.col-xs-height {
  display: table-cell;
  float: none !important;
}

@media (min-width: 768px) {
  .col-sm-height {
    display: table-cell;
    float: none !important;
  }
}
@media (min-width: 992px) {
  .col-md-height {
    display: table-cell;
    float: none !important;
  }
}
@media (min-width: 1200px) {
  .col-lg-height {
    display: table-cell;
    float: none !important;
  }
}
.col-top {
  vertical-align: top;
}
.col-middle {
  vertical-align: middle;
}
.col-bottom {
  vertical-align: bottom;
}


/* No collapse navbar fix */

.navbar-no-collapse .navbar-nav > li {
    float: left;
}
.navbar-no-collapse .navbar-form .form-group {
    display: inline-block;
}
.navbar-no-collapse .navbar-left {
    float: left !important;
}
.navbar-no-collapse .navbar-right {
    float: right !important;
}
.navbar-no-collapse .navbar-form .form-control {
    display: inline-block;
	width: auto;
}
.navbar-no-collapse  .navbar-form {
    width: auto;
    border: 0;
    margin-left: 0;
    margin-right: 0;
    padding-top: 0;
    padding-bottom: 0;
    box-shadow: none;
}

